
$bfsize: 10px;


#commentBox {
    margin-top: 2*$bfsize;
}

//文章主体

@media(max-width: 768px) {
    #comment {
        width: 100%;
    }

    #contentParent {
        padding-left: 30px;
        padding-right: 30px;
    }

    article {
        padding: 1.6*$bfsize;
    }
}

#essayDetails {
    border-bottom: 1px dashed #000;


    article {
        margin-right: 1.6*$bfsize;
        font-size: 1.6*$bfsize;
        padding-bottom: 4*$bfsize;
    }
}

.top {
    margin-top: 2.4*$bfsize;
    margin-bottom: 3*$bfsize;
    text-align: center;
}

.top > h3 {
    font-size: 2.8*$bfsize;
    margin-bottom: 2*$bfsize;
}

.top > .fa-tags {
    margin-bottom: 1*$bfsize;
}

.top > span {
    margin-right: 1.6*$bfsize;
}

.top > span:before {
    padding-right: 0.4*$bfsize;
}

article > section {
    margin-bottom: 5*$bfsize;

    p {
        line-height: 2*$bfsize;
    }

    .zxyVideo {
        width: 100%;
        padding: 16px;
    }
}

.down {
    text-align: center;
}

.down > span {
    font-size: 2.4*$bfsize;
    cursor: pointer;
    margin-right: 4*$bfsize;
}

.down > span:before {
    padding-right: 1*$bfsize;
}

.top .fa-tags > label {
    margin-right: 0.2*$bfsize;
}

img.img-responsive {
    display: inline;
    max-width: 100%;
    // height: auto;
}

/* 侧边栏目录 */
#myScrollspy {
    /*position:relative;*/
    /*margin-top:15*$bfsize;*/
    /*top:15*$bfsize;*/
    /*top: 10*$bfsize;*/
    position: sticky;
    top: 0;

    .nav {
        max-height: 50*$bfsize;
        overflow-y: auto;
        display: block;

        > li {
            display: block;

            a {
                display: block;
                padding-top: 1px;
                padding-bottom: 1px;
                color: #999;
            }

            a.toch1 {
                border-left: 2px solid transparent;
                padding: 2px 0;
                font-size: 1.4*$bfsize;
                font-weight: 400;
            }

            a.toch2 {
                padding-left: 1*$bfsize;
                font-size: 1.3*$bfsize;
            }

            a.toch3 {
                padding-left: 2*$bfsize;
                font-size: 1.2*$bfsize;
            }
        }

        & > li.active > a,
        & > li > a:hover,
        & > li > a:focus {
            /*display: block;*/
            color: #563d7c;
            text-decoration: none;
            background-color: transparent;
            border-left: 2px solid #563d7c;
        }
        /* all active links */
        > .active > a,
        > .active:hover > a,
        > .active:focus > a {
            font-weight: 700;
        }
        /* nested active links */
        /*.bs-docs-sidebar .nav .nav > .active > a,
        .bs-docs-sidebar .nav .nav > .active:hover > a,
        .bs-docs-sidebar .nav .nav > .active:focus > a {
            font-weight: 500;
        }*/
        /* hide inactive nested list */
        ul.nav {
            display: none;
        }
        /* show active nested list */
        > .active > ul.nav {
            display: block;
        }
    }
}

/* 目录下面评论与返回顶部 */
.catelog-aside {
    margin-top: 1*$bfsize;

    a {
        padding-left: 2px;
        font-size: 1.2*$bfsize;
        font-weight: 500;
        color: #999;
        display: block;
    }
}
