$textColor: rgb(79, 79, 79);
@mixin textBase {
    color: $textColor;
    font-family: "Microsoft YaHei", sans-serif, Arial;
    text-size-adjust: 100%;
    box-sizing: border-box;
}

#essayDetails {
    > article {
        > section {
            > h1 {
                display: block;
                font-size: 28px;
                font-weight: 700;
                line-height: 2.8rem;
                margin-bottom: 16px;
                margin-top: 8px;
                text-align: left;
                @include textBase;
            }

            > h2 {
                display: block;
                font-size: 24px;
                font-weight: 700;
                line-height: 2.4rem;
                margin-bottom: 16px;
                margin-top: 8px;
                text-align: left;
                @include textBase;
            }

            > h3 {
                display: block;
                font-size: 22px;
                font-weight: 700;
                line-height: 2.2rem;
                margin-bottom: 16px;
                margin-top: 8px;
                text-align: left;
                @include textBase;
            }

            > blockquote {
                @include textBase;
                box-sizing: border-box;
                color: rgb(33, 37, 41);
                display: block;
                /*font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";*/
                font-size: 16px;
                font-weight: 400;
                line-height: 1.5;
                padding: 10px 20px;
                margin: 0 0 20px;
                border-left: 5px solid #eee;

                > p {
                    span > code {
                        font-size: inherit;
                        line-height: inherit;
                        overflow-wrap: break-word;
                        padding: 2px 4px;
                        border-radius: 4px;
                        margin-right: 2px;
                        margin-left: 2px;
                        color: rgb(164, 75, 2);
                        background: #efefef;
                    }

                    mark {
                        background-color: #ffe58f;
                    }
                }

                > p:last-child {
                    margin-bottom: 0;
                }
            }

            > p {
                @include textBase;
                box-sizing: border-box;
                color: rgb(33, 37, 41);
                display: block;
                /*font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";*/
                font-size: 16px;
                font-weight: 400;
                line-height: 1.5;
                margin-left: 0px;
                margin-right: 0px;
                margin-top: 0px;
                text-align: left;
                text-size-adjust: 100%;

                span > code {
                    font-size: inherit;
                    line-height: inherit;
                    overflow-wrap: break-word;
                    padding: 2px 4px;
                    border-radius: 4px;
                    margin-right: 2px;
                    margin-left: 2px;
                    color: rgb(164, 75, 2);
                    background: #efefef;
                }

                mark {
                    background-color: #ffe58f;
                }

                img {
                    border: 1px solid #5A89C1;
                }
            }

            > p:not(:last-child) {
                margin-bottom: 16px;
            }

            a {
                word-break: break-all;
            }
        }
    }
}
